	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/having-trouble-with-workspaces-in-appcelerator-titanium/index.md absPath of file >>> MarkdownRemark","html":"<p>So I was having some trouble with corrupted workspaces in Appcelerator Titanium. Every time I launched the application it would try opening this corrupt workspace, and now offer any options for selecting a different one.</p>\n<p>The solution? In the end, I just had to resort to deleting the entire <code>.metadata</code> folder of the Workspace.</p>\n<p>Nuclear option maybe, but it worked, and the Titanium Studio was at least able to open for long enough for me to change or rebuild the workspace.</p>","frontmatter":{"title":"Having trouble with workspaces in Appcelerator Titanium?","date":"July 19, 2011","tags":null,"url":null}}},"pathContext":{}}

